/***********************************************************************/
/*** SEARCH OVERLAY ****************************************************/
/***********************************************************************/
.search-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  display: none;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 999;
  left: 0px;
  top: 0px;
}
.search-overlay .search-container {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.search-overlay .close {
  background-color: transparent !important;
  border: none !important;
  padding: 0px;
  margin: 0px;
  line-height: 30px;
  font-size: 30px;
  color: #2B3570;
  position: fixed;
  top: 20px;
  right: 20px;
}
.search-overlay .menu-search-form {
  position: absolute;
  top: 50%;
  width: 100%;
  height: auto;
  margin-top: -40px;
}
.search-overlay .menu-search-form .form-search-btn {
  background-color: transparent;
  border: none;
  color: #2B3570;
  font-size: 48px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  display: none;
}
.search-overlay .menu-search-form .form-search-input {
  background: transparent;
  border: none;
  color: #263238;
  font-size: 58px;
  height: 80px;
  line-height: 80px;
  width: 100%;
  max-width: 900px;
  display: inline-block;
  text-align: center !important;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
}
/***********************************************************************/
/*********** SEARCH RESULTS ********************************************/
/***********************************************************************/
.post-content {
  padding: 100px 0;
  width: 100%;
  height: auto;
  background: #ffffff;
}
.post-content article {
  padding: 40px 0;
  border-bottom: 1px solid #C6383F;
}
.search-link {
  margin-bottom: 20px;
}
/***********************************************************************/
/*********** 404 *******************************************************/
/***********************************************************************/
.error404 {
  display: block;
  width: 100%;
  height: 100vh;
  min-height: auto;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}
.error404 .overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(198, 56, 63, 0.2);
  position: relative;
  text-align: center;
}
.error404 .overlay .row {
  height: 100vh;
}
.error404 h1 {
  position: absolute;
  bottom: 20px;
  left: 0px;
  width: 100%;
  font-size: 175px;
  line-height: 100px;
  color: #ECEFF1;
  opacity: 0.3;
  text-align: center;
  font-family: trumpgothicpro, sans-serif;
  font-weight: 700;
}
.error404 h1 span {
  font-size: 350px;
  line-height: 100px;
}
.error404 p {
  color: #ffffff;
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  margin-bottom: 40px;
}
.pagetext {
  padding-top: 100px;
  position: relative;
}
.btn {
  font-weight: bold;
  border: none!important;
  height: 60px;
  line-height: 60px;
  border-radius: 35px;
  padding: 0 60px;
}
h3 {
  font-size: 5rem;
  font-weight: 900;
}
.page-template-single-template-php h3 {
  font-size: 36px;
  line-height: 1;
  font-family: Montserrat, sans-serif;
  font-weight: 400;
  text-transform: none;
}
.page-template-single-template-php h2 {
  font-family: trumpgothicpro, sans-serif;
  font-size: 36px;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 50px;
}
.page-template-single-template-php p + h2 {
  margin-top: 80px;
}
.page-template-single-template-php p + h3 {
  margin-top: 50px;
}
.blob {
  width: calc(2560px * 2);
  height: calc(2560px * 2);
  border-radius: 100% 100% 0 0;
  background-color: #F9FBFF;
  position: absolute!important;
  content: "";
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) translateY(83%) !important;
}
.home .blob {
  transform: translateX(-50%) translateY(93%) !important;
}
.site {
  overflow: hidden;
}
