footer {
  width: 100%;
  height: auto;
  padding: 20px 0;
  font-size: 12px;
  color: #939EB2;
  padding-bottom: 0;
}
footer div.socials,
footer .copyright,
footer .stealth {
  padding-bottom: 2rem;
}
footer .fab:before {
  color: #939EB2;
  font-size: 33px;
  padding: 10px;
  transition: 1s all;
}
footer .fab:hover:before {
  color: #2B3570;
}
/***********************************************************************/
/* RESPONSE
/*
/* Less style that controll different screen sizes based off bootstrap
/***********************************************************************/
@media (min-width: 1300px) {
  .mobile {
    display: none;
  }
  .desktop {
    display: block;
  }
}
@media (max-width: 1199.98px) {
  .mobile {
    display: none;
  }
  .desktop {
    display: block;
  }
}
@media (max-width: 991.98px) {
}
@media (min-width: 1200.1px) {
  .mobile-menu {
    display: none!important;
  }
}
@media (max-width: 1200px) {
  .mobile-menu {
    display: block!important;
  }
  .desktop-menu {
    display: none!important;
  }
}
@media (max-width: 767.98px) {
  .mobile {
    display: block !important;
  }
  .desktop {
    display: none !important;
  }
  .hide {
    display: none;
  }
  .sm-hide {
    display: none;
  }
  .mobile-text-left {
    text-align: left !important;
  }
  .mobile-text-center {
    text-align: center !important;
  }
  .mobile-text-right {
    text-align: right !important;
  }
  .nav-menu-btn-border .btn {
    padding: 10px;
  }
  .btn-icon-left i,
  .btn-icon-left svg {
    margin-right: 8px;
  }
  .btn-icon-right i,
  .btn-icon-right svg {
    margin-left: 8px;
  }
}
@media (max-width: 575.98px) {
  .xs-hide {
    display: none;
  }
  .btn-icon-left i,
  .btn-icon-left svg {
    margin-right: 5px;
  }
  .btn-icon-right i,
  .btn-icon-right svg {
    margin-left: 5px;
  }
  .btn-success-border {
    height: auto;
  }
}
